html {
    height: 100%;
}

body {
    background: url("https://img.win3000.com/m00/3f/00/dac1664f2324c118e568f1f171a0cc8c.png");
    background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: top;
    overflow: hidden;
    height: 100%;
}
.todo_list_box {
    position: absolute;
    top: 8%;
    left: 50%;
    transform: translateX(-50%);
    width: 360px;
    background-color: #39c5bcad;
}
.todo_list_box>h1 {
    user-select: none;
    text-align: center;
    color: rgb(158, 94, 218);
    transform: translateX(-20px);
}